    filteredContacts() {
      if (this.filterTerm === '') {
        return this.contacts; // Show all records
      } else if (this.filterTerm.toLowerCase() === 'dup') {
        return this.getDuplicateRecords(); // Show only duplicate records
      } else {
        return this.contacts.filter(contact => {
          // Define the specific fields you want to search through
          const fieldsToSearch = [contact.name, contact.phoneNumber, contact.status]; // Add more fields if needed
          return fieldsToSearch.some(value =>
            value.toString().toLowerCase().includes(this.filterTerm.toLowerCase())
          );
        });
      }
    }
