  methods: {
    isValidEmail(email) {
      // Regular expression for validating an email address
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },
    previousContact() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
    nextContact() {
      if (this.currentIndex < this.contacts.length - 1) {
        this.currentIndex++;
      }
    },
    isBlank(ss) {
      return ss === null || ss === undefined || ss.trim() === '';
    },
    formattedPhoneNumber(ss) {
      if (this.isBlank(ss)) return '';

      // Extract country code and local number
      const countryCode = ss.substring(0, 3);
      const localNumber = ss.substring(3);

      // Format the phone number
      return `(${countryCode}) ${localNumber}`;
    },

    async loadContactsFromStrapi(projectFilter) {
      // Prompt the user for how to handle existing contacts
      const action = confirm('Do you want to overwrite existing contacts or append the new ones? Click "OK" to overwrite or "Cancel" to append.');

      // If the user selects "OK", clear existing contacts
      if (action) {
        this.contacts = [];
      }

      const fetchContactsQuery = gql`
    query($project: String!) {
      leads(filters: { project: { eq: $project } }, pagination: { limit: 1000 }) {
        data {
          id
          attributes {
            project
            user
            name
            phoneNumber
            status
            interaction
            lastCall
            nextCall
            called
            contacted
            language
            message
            doc
            na1
            source
            occupation
            updatedAt
          }
        }
      }
    }
  `;

      try {
        const { data } = await client.query({
          query: fetchContactsQuery,
          variables: { project: projectFilter },
          fetchPolicy: 'no-cache',
        });

        // Iterate over each contact fetched from Strapi
        data.leads.data.forEach((lead) => {
          const existingContact = this.contacts.find(contact => contact.phoneNumber === lead.attributes.phoneNumber);

          const newContact = {
            no: this.contacts.length + 1,
            user: lead.attributes.user || '',
            name: lead.attributes.name || '',
            phoneNumber: lead.attributes.phoneNumber || '',
            project: lead.attributes.project || '',
            occupation: lead.attributes.occupation || '',
            status: lead.attributes.status || '',
            interaction: lead.attributes.interaction || '',
            lastCall: this.convertYMD2DMY(lead.attributes.lastCall) || '1-1-1900',
            nextCall: this.convertYMD2DMY(lead.attributes.nextCall) || '1-1-1900',
            called: lead.attributes.called || 0,
            contacted: lead.attributes.contacted || 0,
            language: lead.attributes.language || '',
            message: lead.attributes.message || '',
            doc: lead.attributes.doc || '',
            na1: lead.attributes.na1 || '',
            source: lead.attributes.source || '',
            edit: false,
            editField: '',
            updatedAt: lead.attributes.nextCall.toString()
          };

          if (existingContact && !action) {
            // If appending and contact exists, update it
            Object.assign(existingContact, newContact);
          } else {
            // Add new contact
            this.contacts.push(newContact);
          }
        });

        // Sort contacts by phoneNumber and remove duplicates
        //  this.contacts.sort((a, b) => (a.phoneNumber < b.phoneNumber ? -1 : 1));
        //   this.removeDuplicatePhoneNumbers();
        this.sortContactsAndNumber('project');
      } catch (error) {
        console.error('Error loading contacts from Strapi:', error);
      }
    },

    convertYMD2DMY(dateString) {
      // Check if dateString is valid
      if (!dateString) return null;

      // Parse the date string to a Date object
      const date = new Date(dateString);

      // Check if the date is the default invalid date (1900-01-01)
      if (date.getFullYear() === 1900 && date.getMonth() === 0 && date.getDate() === 1) {
        return null;
      }

      // Format the date as DD/MM/YYYY
      const dayStr = String(date.getDate()).padStart(2, '0');
      const monthStr = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
      const yearStr = date.getFullYear();

      return `${dayStr}/${monthStr}/${yearStr}`;
    },
    convertDMY2YMD(dateString) {
      // Check if dateString is valid
      if (!dateString) return null;

      // Replace different delimiters with '/'
      const formattedDateString = dateString.replace(/-/g, '/');

      // Split the date string into components
      const [day, month, year] = formattedDateString.split('/').map(Number);

      // Validate date components
      if (isNaN(day) || isNaN(month) || isNaN(year)) {
        console.error('Invalid date components');

        return null;
      }

      // Create a new Date object with the components
      const date = new Date(year, month - 1, day); // Month is zero-based in JavaScript

      // Check if the date was created correctly
      if (date.getFullYear() !== year || date.getMonth() !== month - 1 || date.getDate() !== day) {
        console.error('Invalid date');
        return null;
      }

      // Format the date as YYYY-MM-DDTHH:mm:ss
      const yearStr = date.getFullYear();
      const monthStr = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
      const dayStr = String(date.getDate()).padStart(2, '0');
      const hoursStr = '00';
      const minutesStr = '00';
      const secondsStr = '00';

      // Return null for invalid default date
      if (yearStr === '1900') {
        return null;
      }

      return `${yearStr}-${monthStr}-${dayStr}T${hoursStr}:${minutesStr}:${secondsStr}Z`;
    }
    ,
    async exportToStrapi() {
      const createMutation = gql`
    mutation CreateLead(
      $no: Int
      $user: String
      $name: String
      $phoneNumber: String
      $status: String
      $interaction: String
      $lastCall: DateTime
      $nextCall: DateTime
      $called: Int
      $contacted: Int
      $language: String
      $message: String
      $doc: String
      $na1: String
      $source: String
      $project: String
      $email: String
      $date: DateTime
    ) {
      createLead(
        data: {
          user: $user
          name: $name
          phoneNumber: $phoneNumber
          status: $status
          interaction: $interaction
          lastCall: $lastCall
          nextCall: $nextCall
          called: $called
          no: $no
          language: $language
          contacted: $contacted
          message: $message
          doc: $doc
          na1: $na1
          source: $source
          project: $project
          email: $email
          date: $date
        }
      ) {
        data {
          id
          attributes {
            name
            phoneNumber
            status
            interaction
            lastCall
            nextCall
            no
            project
            source
          }
        }
      }
    }
  `;

      const updateMutation = gql`
    mutation UpdateLead(
      $id: ID!
      $no: Int
      $user: String
      $name: String
      $phoneNumber: String
      $status: String
      $interaction: String
      $lastCall: DateTime
      $nextCall: DateTime
      $called: Int
      $contacted: Int
      $language: String
      $message: String
      $doc: String
      $na1: String
      $source: String
      $project: String
      $email: String
      $date: DateTime
    ) {
      updateLead(
        id: $id
        data: {
          user: $user
          name: $name
          phoneNumber: $phoneNumber
          status: $status
          interaction: $interaction
          lastCall: $lastCall
          nextCall: $nextCall
          called: $called
          no: $no
          language: $language
          contacted: $contacted
          message: $message
          doc: $doc
          na1: $na1
          source: $source
          project: $project
          email: $email
          date: $date
        }
      ) {
        data {
          id
          attributes {
            name
            phoneNumber
            status
            interaction
            lastCall
            nextCall
            no
          }
        }
      }
    }
  `;

      this.totalContacts = this.contacts.length;
      this.updatedCount = 0; // Reset count at the start

      try {
        for (const contact of this.contacts) {
          const existingContactQuery = gql`
        query GetLead($phoneNumber: String!) {
          leads(filters: { phoneNumber: { eq: $phoneNumber } }) {
            data {
              id
              attributes {
                no
                source
                project
                name
                phoneNumber
                status
                interaction
                lastCall
                nextCall
                called
                contacted
                language
                message
                doc
                na1
                updatedAt
                email
                date
              }
            }
          }
        }
      `;

          const { data } = await client.query({
            query: existingContactQuery,
            variables: { phoneNumber: contact.phoneNumber },
            fetchPolicy: 'no-cache',
          });

          if (Array.isArray(data.leads.data) && data.leads.data.length > 0) {
            const existingContact = data.leads.data[0].attributes;

            if (existingContact.source !== contact.source) {
              alert(`${contact.phoneNumber} exists in both server ${existingContact.source} and local ${contact.source}`);
              continue;
            }

            if (existingContact.name !== contact.name) {
              alert(`${contact.name} with phone number ${contact.phoneNumber} exists in both server ${existingContact.name} and local ${contact.name}`);
              continue;
            }

            if (existingContact.updatedAt == contact.updatedAt) {
              continue;
            }

            const fieldsToUpdate = {};
            for (const field in contact) {
              if (contact[field] !== existingContact[field] && field !== 'id') {
                fieldsToUpdate[field] = contact[field];
              }
            }

            if (Object.keys(fieldsToUpdate).length > 0) {
              await client.mutate({
                mutation: updateMutation,
                variables: {
                  id: data.leads.data[0].id,
                  ...fieldsToUpdate,
                  contacted: parseInt(contact.contacted, 10),
                  called: parseInt(contact.called, 10),
                  date: this.convertDMY2YMD(contact.date),
                  email: this.isValidEmail(contact.email) ? contact.email : null, // Conditional check for email
                  lastCall: this.convertDMY2YMD(contact.lastCall),
                  nextCall: this.convertDMY2YMD(contact.nextCall),
                },
              });
              this.updatedCount++;
            }
          } else {
            await client.mutate({
              mutation: createMutation,
              variables: {
                ...contact,
                no: parseInt(contact.no, 10),
                contacted: parseInt(contact.contacted, 10),
                called: parseInt(contact.called, 10),
                date: this.convertDMY2YMD(contact.date),
                email: this.isValidEmail(contact.email) ? contact.email : null, // Conditional check for email
                lastCall: this.convertDMY2YMD(contact.lastCall),
                nextCall: this.convertDMY2YMD(contact.nextCall),
              },
            });
          }

          this.progressPercentage = (this.updatedCount / this.totalContacts) * 100;
          console.log(`Progress: ${this.progressPercentage.toFixed(2)}%`);
        }

        alert('Data synchronization completed!');
      } catch (error) {
        console.error('Error syncing contacts with Strapi:', error);
        alert('Error syncing contacts with Strapi: ' + error);
      }
    },


    // navigateToSocket() {
    //   this.$router.push({ name: 'SocketComponent' });
    // },
    removeContact(index) {
      // Get the contact from the filtered list
      const contactToRemove = this.filteredContacts[index];

      // Confirm with the user before deleting
      const confirmation = confirm(`Are you sure you want to delete the contact: ${contactToRemove.name} (${contactToRemove.phoneNumber})?`);

      // If the user confirms, proceed with the deletion
      if (confirmation) {
        // Find the index of this contact in the original contacts array
        const originalIndex = this.contacts.findIndex(contact => contact === contactToRemove);

        // Debugging: Log the original index and the contact to be removed
        console.log('Removing contact at original index:', originalIndex);
        console.log('Contact:', this.contacts[originalIndex]);

        // Remove the contact from the original contacts array if it exists
        if (originalIndex !== -1) {
          this.contacts.splice(originalIndex, 1);
        }
      } else {
        // If the user cancels, log a message (optional)
        console.log('Contact removal canceled.');
      }
    },
    getDuplicateRecords() {
      const phoneNumberCount = this.contacts.reduce((acc, contact) => {
        const phoneNumber = contact.phoneNumber.trim();
        if (phoneNumber) {
          acc[phoneNumber] = (acc[phoneNumber] || 0) + 1;
        }
        return acc;
      }, {});

      const duplicateNumbers = Object.keys(phoneNumberCount).filter(number => phoneNumberCount[number] > 1);

      return this.contacts.filter(contact => duplicateNumbers.includes(contact.phoneNumber.trim()));
    },
    stopCountdownFromParent() {
      this.$refs.countdown.stopCountdown();
    },
    async sendMessage() {
      this.currentIndex = 0;
      while (this.currentIndex < this.contacts.length) {
        // Check if the current contact has already been sent a message
        //   if (this.contacts[this.currentIndex].status.trim() !== 'send' && this.contacts[this.currentIndex].phoneNumber.trim().length > 0) {

        if (
          !this.contacts[this.currentIndex].status.trim().toLowerCase().includes('send') &&
          !this.contacts[this.currentIndex].status.trim().toLowerCase().includes('xxx') &&
          this.contacts[this.currentIndex].phoneNumber.trim().length > 0
        ) {

          const contact = this.contacts[this.currentIndex];
          let language = "e";

          if (contact.language !== undefined && contact.language.trim() !== "") {
            language = contact.language;
          }


          try {
            // if (contact.status.trim().length > 0) {
            //   alert(contact.status + ":" + contact.phoneNumber);

            // }

            let message = "!" + language + this.sendmsg;
            let msg = this.getmessage(message, contact);
            contact.status = "send";
            if (contact.doc !== undefined)
              contact.doc = this.sendmsg + contact.doc;
            else
              contact.doc = this.sendmsg


            //      alert("calling :" + contact.name + ":" +  contact.phoneNumber + ":"+ msg);

            const response = await axios.post('http://localhost:3000/send-message', {
              number: contact.phoneNumber,
              message: msg
            });

            if (response.data.success) {
              // Optionally handle successful message sending
            } else {
              alert('Failed to send message: ' + response.data.error);
              contact.status = "error 1 send";
              this.$refs.countdown.stopCountdown();
              return; // Exit the function if message sending fails
            }
          } catch (error) {
            console.error('Error:', error);
            contact.status = "error 2 send";
            alert('An error occurred: ' + error.message);
            this.$refs.countdown.stopCountdown();
            return; // Exit the function if an error occurs
          }
          this.currentIndex++;
          break;
        }
        // Move to the next contact
        this.currentIndex++;

      }

      if (this.currentIndex >= this.contacts.length) {
        this.$refs.countdown.stopCountdown();
        alert("send whatapp message completed");
      }
    },

    isVisible(contact) {
      if (this.filter === 'showAll') {
        return true; // Show all records
      }
      // Your existing filter logic
      // Example:
      return contact.phoneNumber && contact.phoneNumber.includes(this.searchTerm);
    },
    countdownFinished() {
      console.log("Countdown finished!");
      this.sendMessage();
      // You can add any additional logic you need here
    },
    sortContacts(property) {
      if (this.sortOrder[property] === 'asc') {
        this.contacts.sort((a, b) => {
          if (a[property] < b[property]) return -1;
          if (a[property] > b[property]) return 1;
          return 0;
        });
        this.sortOrder[property] = 'desc';
      } else {
        this.contacts.sort((a, b) => {
          if (a[property] < b[property]) return 1;
          if (a[property] > b[property]) return -1;
          return 0;
        });
        this.sortOrder[property] = 'asc';
      }
    },
    formatNumberWithLeadingZeros(number) {
      // Convert the number to a string
      const numStr = String(number);
      // Pad the string with leading zeros
      const paddedStr = numStr.padStart(6, '0');
      return paddedStr;
    },
    sortContactsAndNumber(property) {
      if (this.sortOrder[property] === 'asc') {
        this.contacts.sort((a, b) => {
          if (a[property] + this.formatNumberWithLeadingZeros(a['no']) < b[property] + this.formatNumberWithLeadingZeros(b['no'])) return -1;
          if (a[property] + this.formatNumberWithLeadingZeros(a['no']) > b[property] + this.formatNumberWithLeadingZeros(b['no'])) return 1;
          return 0;
        });
        this.sortOrder[property] = 'desc';
      } else {
        this.contacts.sort((a, b) => {
          if (a[property] + a['no'] < b[property] + a['no']) return 1;
          if (a[property] + a['no'] > b[property] + a['no']) return -1;
          return 0;
        });
        this.sortOrder[property] = 'asc';
      }
    },

