    generateDateOptions() {
      const dates = [];
      const today = new Date();
      for (let i = 0; i < 30; i++) {
        const date = new Date(today);
        date.setDate(today.getDate() + i);

        const day = String(date.getDate()).padStart(2, '0'); // Get day and pad with leading zero if needed
        const month = String(date.getMonth() + 1).padStart(2, '0'); // Get month (zero-based) and pad with leading zero if needed
        const year = date.getFullYear(); // Get full year

        const formattedDate = `${day}/${month}/${year}`; // Format as DD/MM/YYYY
        dates.push(formattedDate);
      }
      return dates;
    },
    addPlus(np) {
      if (np.substring(0, 2) === "60")
        np = "+" + np;
      return np.replaceAll("@", "");
    },
    formatString(text) {
      if (text === undefined) {
        return '';
      }

      // Remove all non-numeric characters
      text = text.replace(/\D/g, '');

      if (text.length <= 12) {
        return text.padEnd(12, '_'); // Pad with underscores if shorter
      } else {
        return text.slice(0, 12); // Truncate if longer
      }
    },
    loadMessageFiles() {
      this.loadTextFile('/messageEn.txt', 'messageEn');
      this.loadTextFile('/messageCn.txt', 'messageCn');
      //  this.loadTextFile('/messageBs.txt', 'messageBs');

    },
    loadTextFile(filePath, lngName) {
      fetch(filePath)
        .then(response => response.text())
        .then(data => {
          console.log(`File loaded successfully: ${filePath}`);
          console.log(`Data: ${data}`);
          const lines = data.split('\n');
          const dataArray = [];
          let currentObject = { code: '', msg: '' };

          if (this.projectKey === "undefined") {
            alert("ProjectKey Not defined");
            return;
          }

          lines.forEach(line => {
            if (!line.startsWith('_')) {
              if (line.startsWith('@')) {
                if (currentObject.code !== '') {
                  dataArray.push(currentObject);
                }
                const [code, msg] = line.split('|');
                currentObject = { code: code.trim().substring(1), msg: msg.trim() + "\n" };
              } else {
                currentObject.msg += line.trim().replaceAll("$plink", "https://alvintkb.github.io/?p=gyp" + this.enDate + "$phone" + this.projectKey) + "\n";
              }
            }
          });

          if (currentObject.code !== '') {
            dataArray.push(currentObject);
          }

          this[lngName].splice(0, this[lngName].length, ...dataArray);
          if (this[lngName].length == 0) {
            alert("Error! " + lngName + " no data");
          }
        })
        .catch(error => {
          console.error('Error loading text file:', error);
        });
    },
    sendmessage() {
      if (this.phoneNumber && this.whatsappmessage) {
        let msg = this.getmessage(this.whatsappmessage);

        const textarea = document.createElement('textarea');
        textarea.value = msg;
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);

        const cleanedPhoneNumber = this.cleanPhoneNumber(this.phoneNumber);
        const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
        window.open(url, '_blank');
      } else {
        alert('Please enter both telephone number and WhatsApp message.');
      }
    },
    jumpToIndex() {
      if (this.findValue >= 0 && this.findValue < this.contacts.length) {
        this.currentIndex = this.findValue - 1;
      } else {
        console.error("Invalid index.");
      }
    },
    findContactByNumber(phoneNumber) {
      return this.contacts.find(contact => contact.phoneNumber.includes(phoneNumber));
    },
    findNumber() {
      const matchingContact = this.findContactByNumber(this.findValue);
      if (matchingContact) {
        let i = this.contacts.indexOf(matchingContact);
        this.currentIndex = i;
      } else {
        console.error("No contact found with phone number containing", this.findValue);
      }
    },
    findName() {
      const matchingContact = this.findByName(this.findValue);
      if (matchingContact) {
        let i = this.contacts.indexOf(matchingContact);
        this.currentIndex = i;
      } else {
        console.error("No contact found with this name containing", this.findValue);
      }
    },
    findByName(name) {
      return this.contacts.find(contact => contact.name.toLowerCase().includes(name.toLowerCase()));
    },
    camelize(str) {
      return str.replace(/(?:^\w|[-\s]\w)/g, function (match) {
        return match.toUpperCase();
      }).replace(/[-_]/g, '');
    },
    rowSendWhatsApp(contact) {
      // Validate the input message. If not valid, set the default message code 'a0'.
      if (!this.validateInput(this.sendmsg)) {
        this.sendmsg = 'a0';
      }

      // Extract the phone number and language from the contact object.
      const { phoneNumber, language } = contact;

      // Construct the message code with language prefix (default to 'e' for English).
      var message = '!' + (language || 'e') + this.sendmsg;

      // Generate the full message content using the getmessage function.
      let msg = this.getmessage(message, contact);

      // Copy the generated message to the clipboard.
      const textarea = document.createElement('textarea');
      textarea.value = msg;
      document.body.appendChild(textarea);
      textarea.select();
      document.execCommand('copy');
      document.body.removeChild(textarea);

      // Clean the phone number (remove unnecessary characters).
      const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);

      // Construct the WhatsApp URL with the cleaned phone number and encoded message.
      const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
      window.open(url, '_blank'); // Open the WhatsApp link in a new tab.

      // Update the 'doc' field for the current contact with the sent message code.
      if (this.contacts[this.currentIndex].doc.length > 0) {
        this.contacts[this.currentIndex].doc += "," + this.sendmsg;
      } else {
        this.contacts[this.currentIndex].doc += this.sendmsg;
      }

      // Update the currentIndex to the index of the current contact.
      this.currentIndex = this.contacts.findIndex(c => c.no === contact.no);
    },
    sendwhatsappmessage() {
      const { phoneNumber, na1, name, message } = this.currentContact;
      if (na1 !== 'w') {
        this.moveToNextContact();
        return;
      }
      if (this.validateInput(na1)) {
        let msg = this.getmessage(message);
        msg = msg.replaceAll("@name", name);
        msg = msg.replaceAll("$phone", this.formatString(this.contacts[this.currentIndex].phoneNumber));

        const textarea = document.createElement('textarea');
        textarea.value = msg;
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);

        const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);
        const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
        window.open(url, '_blank');

        this.moveToNextContact();
      }
    },
    getmessage(message, contact) {

      let msg = "";

      if (message.slice(0, 2) === "!c")
        msg = this.messageCn.find(msg => msg.code === message.slice(1)).msg;
      // else if (message.slice(0, 2) === "!b")
      //   msg = this.messageBs.find(msg => msg.code === message.slice(1)).msg;
      else if (message.slice(0, 2) === "!e" || message.slice(0, 2) === "!b") {
        if (this.messageEn.length == 0) {
          alert("No Data");
          msg = "";
        }
        else
          msg = this.messageEn.find(msg => msg.code === message.slice(1)).msg;
      }
      else {
        alert("message error on 1st character, only allow e c b:" + message);
      }
      msg = msg.replaceAll("@name", contact.name);
      msg = msg.replaceAll("@project", this.camelize(contact.project));
      msg = msg.replaceAll("$phone", this.formatString(contact.phoneNumber));

      return msg;
    },
    validateInput(message) {
      if (!message) {
        // alert('Please enter a message.');
        //  this.error = 'Please enter a message.';
        return false;
      }
      this.error = null;
      return true;
    },
    moveToNextContact() {
      let checkedContacts = 0;
      do {
        this.currentIndex++;
        if (this.currentIndex >= this.contacts.length) {
          this.currentIndex = 0;
          checkedContacts++;
          if (checkedContacts >= this.contacts.length) {
            break;
          }
        }
      } while (this.contacts[this.currentIndex].na1 !== 'w');
    },
    startProcessing() {
      this.currentIndex = 0;
      let checkedContacts = 0;
      while (this.contacts[this.currentIndex].na1 !== 'w') {
        this.moveToNextContact();
        checkedContacts++;
        if (checkedContacts >= this.contacts.length) {
          break;
        }
      }
    },

    loadJSONFile(event) {
      const file = event.target.files[0];
      if (!file) {
        this.error = 'Please select a file.';
        return;
      }
      localStorage.removeItem('appData');
      this.contacts = [];
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const jsonData = JSON.parse(e.target.result);
          this.contacts = jsonData.map((contact, index) => ({
            no: index + 1,
            name: contact.name ? contact.name.trim() : '',
            language: contact.language ? contact.language.trim() : '',
            phoneNumber: contact.phoneNumber ? this.addPlus(contact.phoneNumber.trim()) : '',
            email: contact.email ? String(contact.email).trim() : '',
            status: contact.status ? contact.status.trim() : '',
            date: contact.date ? contact.date.trim() : '1-1-1900',
            project: contact.project ? contact.project.trim() : '',
            source: contact.source ? contact.source.trim() : '',
            doc: contact.doc ? contact.doc.trim() : '',
            called: contact.called ? String(contact.called).trim() : '0',
            contacted: contact.contacted ? String(contact.contacted).trim() : '0',
            occupation: contact.occupation ? contact.occupation.trim() : '',
            na1: contact.na1 ? contact.na1.trim() : '',
            message: contact.message ? contact.message.trim() : '',
            interaction: contact.interaction ? contact.interaction.trim() : '',
            nextCall: contact.nextCall ? contact.nextCall.trim() : '1-1-1900',
            lastCall: contact.lastCall ? contact.lastCall.trim() : '1-1-1900',
            edit: false,
            editField: ''
          }));

          // Sort contacts by phoneNumber
          this.contacts.sort((a, b) => {
            if (a.phoneNumber < b.phoneNumber) return -1;
            if (a.phoneNumber > b.phoneNumber) return 1;
            return 0;
          });
          // Remove duplicates
          //  this.removeDuplicatePhoneNumbers();
          this.resetJsonFileInput();
        } catch (error) {
          console.error('Error parsing JSON file:', error);
          this.error = 'Error parsing JSON file.';
          this.resetJsonFileInput();
        }
      };
      reader.onerror = (error) => {
        console.error('Error reading file:', error);
        this.error = 'Error reading file.';
      };
      reader.readAsText(file);
    },
    formatPhoneNumber(phoneNumber) {
      if (!phoneNumber.startsWith("@")) {
        return "@" + phoneNumber;
      }
      return phoneNumber;
    },
    removeDuplicatePhoneNumbers() {
      const seen = new Set();
      this.contacts = this.contacts.filter(contact => {
        // Check if the phoneNumber is blank or already seen
        if (!contact.phoneNumber || seen.has(contact.phoneNumber)) {
          return false;
        } else {
          seen.add(contact.phoneNumber);
          return true;
        }
      });
    },
    loadCSVFile(event) {
      const file = event.target.files[0];
      if (!file) {
        this.error = 'Please select a file.';
        return;
      }

      const reader = new FileReader();
      reader.onload = (e) => {
        const csvData = e.target.result;
        Papa.parse(csvData, {
          header: true,
          complete: (results) => {
            const newContacts = results.data.map((contact, index) => ({
              no: index + 1,
              name: contact.name ? contact.name.trim() : '',
              phoneNumber: contact.phoneNumber ? this.addPlus(contact.phoneNumber.trim()) : '',
              email: contact.email ? contact.email.trim() : '',
              status: contact.status ? contact.status.trim() : '',
              date: contact.date ? contact.date.trim() : "1-1-1900",
              project: contact.project ? contact.project.trim() : '',
              source: contact.source ? contact.source.trim() : '',
              occupation: contact.occupation ? contact.occupation.trim() : '',
              na1: contact.na1 ? contact.na1.trim() : '',
              message: contact.message ? contact.message.trim() : '',
              interaction: contact.interaction ? contact.interaction.trim() : '',
              nextCall: contact.nextCall ? contact.nextCall.trim() : "1-1-1900",
              lastCall: contact.lastCall ? contact.lastCall.trim() : "1-1-1900",
              edit: false,
              editField: ''
            }));
            this.promptUser(newContacts);
            this.resetFileInput();
          },
          error: (error) => {
            console.error('Error parsing CSV file:', error);
            this.error = 'Error parsing CSV file.';
            this.resetFileInput();
          }
        });
        this.sortContactsAndNumber('project');
      };
      reader.readAsText(file);
    },
    exportToCSV() {

      const csvA = `source,project,name,phoneNumber,message,interaction,status,date,occupation,na1,email`;
      const csvB = this.contacts.map(contact => {
        // Check if date is blank or undefined
        const date = contact.date ? contact.date.trim() : '';
        const currentDate = date ? date : new Date().toLocaleDateString('en-CA'); // Adjust date format as needed
        return `${contact.source},${contact.project},${contact.name},${this.formattedPhoneNumber(contact.phoneNumber)},${contact.message},${contact.interaction},${contact.status},${currentDate},${contact.occupation},${contact.na1},${contact.message},${contact.email}`;
      }).join('\n');

      const csvData = `${csvA}\n${csvB}`;
      //const csvOut = [...csvB];
      const blob = new Blob([csvData], { type: 'text/csv;charset=utf-8;' });
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', 'contacts.csv');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    resetFileInput() {
      // Reset the file input value so that the same file can be selected again
      this.$refs.fileInput.value = '';
    },
    resetJsonFileInput() {
      // Reset the file input value so that the same file can be selected again
      this.$refs.jsonFileInput.value = '';
    },
    promptUser(newContacts) {
      if (confirm('Do you want to overwrite the existing contacts? Click "OK" to overwrite, "Cancel" to append.')) {
        this.contacts = newContacts;
      } else {
        this.contacts = [...this.contacts, ...newContacts];
        //   this.removeDuplicatePhoneNumbers();
      }
    },

    exportToJSON() {
      const jsonData = JSON.stringify(this.contacts, null, 2);
      const blob = new Blob([jsonData], { type: 'application/json;charset=utf-8;' });
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', 'contacts.json');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    editField(contact, field) {
      contact.edit = true;
      contact.editField = field;
    },
    saveField(contact) {
      contact.edit = false;
      contact.editField = '';
    },



  }


};
</script>


